/* Block Group */
/* Lists */
.article__content ol,
.article__content ul {
  margin-bottom: 20px;
  padding-left: 13px;
}
@media screen and (min-width: 992px) {
  .article__content ol,
.article__content ul {
    margin-bottom: 40px;
  }
}
.article__content ul {
  list-style: square;
}

/* hr */
.wp-block-separator {
  border-color: #dcdcdc;
  margin-bottom: 20px;
  margin-top: 20px;
}

/* Quote */
.wp-block-quote {
  background-color: #f8f9fa;
  border-left-width: 3px;
  margin-bottom: 40px;
  padding: 15px 10px;
}
@media screen and (min-width: 992px) {
  .wp-block-quote {
    padding: 30px 20px;
  }
}
.wp-block-quote p {
  font-weight: 600;
  font-size: 1.5em;
  line-height: 1.3;
}
.wp-block-quote cite {
  font-size: 0.87em;
  font-weight: 700;
  letter-spacing: 1px;
  text-transform: uppercase;
}
@media screen and (min-width: 992px) {
  .wp-block-quote cite {
    margin-left: 20px;
  }
}

/* Code */
.wp-block-code code {
  background-color: #f8f9fa;
  border: none;
  border-radius: 0;
  color: inherit;
  padding: 20px;
}
@media screen and (min-width: 992px) {
  .wp-block-code code {
    padding: 40px;
  }
}

/* Table */
.wp-block-table {
  margin-bottom: 40px;
}
.wp-block-table table {
  margin-bottom: 10px;
}
.wp-block-table thead {
  background-color: #0e0e0e;
  color: #fff;
  text-align: left;
}
.wp-block-table th,
.wp-block-table td {
  padding: 10px;
}
.wp-block-table figcaption {
  color: #0e0e0e;
  font-size: 14px;
  opacity: 0.5;
}
.wp-block-table.is-style-regular thead,
.wp-block-table.is-style-regular th {
  border: none;
}
.wp-block-table.is-style-regular td {
  border-color: #dcdcdc;
}
.wp-block-table.is-style-stripes {
  border: none;
}
.wp-block-table.is-style-stripes tbody {
  border: 1px solid #dcdcdc;
}
.wp-block-table.is-style-stripes tbody tr:nth-child(odd) {
  background-color: #f8f9fa;
}

body {
  color: #0e0e0e;
  font-family: "Inter", sans-serif;
  font-weight: 400;
}

body {
  font-size: 14px;
}

h1 {
  font-size: 2.6em;
}

h2 {
  font-size: 2.15em;
}

h3 {
  font-size: 1.7em;
}

h4 {
  font-size: 1.25em;
}

h5 {
  font-size: 1em;
}

h6 {
  font-size: 0.85em;
}

@media screen and (min-width: 992px) {
  body {
    font-size: 16px;
  }

  h1 {
    font-size: 2.6em;
  }

  h2 {
    font-size: 2.15em;
  }

  h3 {
    font-size: 1.7em;
  }

  h4 {
    font-size: 1.25em;
  }

  h5 {
    font-size: 1em;
  }

  h6 {
    font-size: 0.85em;
  }
}
h1,
.h1,
h2,
.h2,
h3,
.h3,
h4,
.h4,
h5,
.h5,
h6,
.h6 {
  font-weight: 600;
  margin: 0;
  word-wrap: break-word;
}
h1:not(:last-child),
.h1:not(:last-child),
h2:not(:last-child),
.h2:not(:last-child),
h3:not(:last-child),
.h3:not(:last-child),
h4:not(:last-child),
.h4:not(:last-child),
h5:not(:last-child),
.h5:not(:last-child),
h6:not(:last-child),
.h6:not(:last-child) {
  margin-bottom: 10px;
}
@media screen and (min-width: 992px) {
  h1:not(:last-child),
.h1:not(:last-child),
h2:not(:last-child),
.h2:not(:last-child),
h3:not(:last-child),
.h3:not(:last-child),
h4:not(:last-child),
.h4:not(:last-child),
h5:not(:last-child),
.h5:not(:last-child),
h6:not(:last-child),
.h6:not(:last-child) {
    margin-bottom: 20px;
  }
}

p,
ul,
ol {
  line-height: 1.5;
  margin-bottom: 20px;
  margin-top: 0;
  word-wrap: break-word;
}

a {
  color: #6366f1;
  outline: 0;
  transform: 0.3s;
  transition: 0.3s;
}
a:hover, a:focus {
  color: #c1c2f9;
}
a:link, a:visited, a:hover, a:active {
  text-decoration: none;
}
a.text-primary:hover, a.text-primary:focus {
  color: #c1c2f9;
}

span {
  word-wrap: break-word;
}

table {
  width: 100%;
}

p img {
  margin-bottom: 0.5em;
  margin-top: 0.4em;
}

img.header-image,
img.wp-post-image,
img.avatar,
img[class*=align],
img[class*=wp-image-],
img[class*=attachment-],
.comment-content img,
.widget img {
  height: auto;
  max-width: 100%;
  padding: 0;
}

mark,
ins {
  background: #fff9c0;
  text-decoration: none;
}

.sticky {
  display: block;
}

.alignright {
  display: inline;
  float: right;
  margin-left: 15px;
}

.alignleft {
  display: inline;
  float: left;
  margin-right: 15px;
}

.aligncenter {
  clear: both;
  display: block;
  margin-left: auto;
  margin-right: auto;
  text-align: center;
}

/* Editor Width */
html:where(.editor-styles-wrapper) {
  color: #0e0e0e;
  font-family: "Inter", sans-serif;
}

.wp-block {
  max-width: 1024px;
}

.wp-block[data-align=wide] {
  max-width: 1400px;
}

.wp-block[data-align=full] {
  max-width: none;
}

/* 1 Columns Hack */
.wp-block-columns > .editor-inner-blocks > .editor-block-list__layout > [data-type="core/column"] {
  flex-basis: 100%;
}